.Opcoes--Container{
    display: flex;
    flex-direction: column;
  }

.Opc{
    display: flex;
    flex-direction: column;
}
  
  .acertoOUerro{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    width: 60%;
    height: 100%;
  }
  
  .Botao_acertoOUerro{
    width: 130px;
    height: 45px;
    border-radius: 8px;
    margin-left: 20px;
  }
  
  .ganharOUperder{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    width: 60%;
    height: 100%;
  }
  
  .Botao_ganharOUperder{
    width: 130px;
    height: 45px;
    border-radius: 8px;
    margin-left: 20px;
  }

  .Narracao{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    width: 60%;
    height: 100%;
  }

  .Botao_Narracao{
    width: 130px;
    height: 45px;
    border-radius: 8px;
    margin-left: 20px;
  }

  .VoltarOpc{
    display: flex;
    align-items: flex-end;
    justify-content: flex-start;
    height: 100%;
    margin-left: 50px;
}
  
  .Botao_Voltar{
    width: 130px;
    height: 45px;
    justify-content: flex-start;
    align-items: flex-start;
    border-radius: 8px;
    background-color: silver;
  }